* {
  font-family: "Roboto Slab", serif;
  color: #ffd700;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.bg-rose {
  background: #f88379;
}

section {
  box-sizing: border-box;
  width: 100%;
  height: 101vh;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  padding: 50px;
}

section article {
  display: block;
  transform: translateX(-100px);
  opacity: 0;
}

.text1 {
  animation: myColor 5s linear 2s infinite alternate;
}
@keyframes myColor {
  from {
    color: #ffd700;
  }
  to {
    color: red;
  }
}

ul li {
  list-style: none;
}
li::before {
  content: "❤️"; /* Insert content that looks like bullets */
  padding-right: 8px;
}

img {
  transition: transform 0.2s;
  opacity: 0.9;
}

img:hover {
  transform: scale(1.05);
  opacity: 1;
}
